<template>
  <tas-base-crud ref="sync-module" :config="config" hide-create hide-update hide-delete>
    <template v-slot:list-action>
      <button :disabled="isLoadingSync" v-if="$_sys.isAllowed(config.permission.create)" @click="syncData()" class="btn btn-primary btn-circle ml-3">
        <i class="ri-refresh-line p-0"></i> Sync Data </button>
      <b-toast id="export-toast" variant="info" solid no-auto-hide no-close-button :visible="isLoadingSync">
        <template #toast-title>
          <div class="d-flex flex-grow-1 align-items-center pt-2">
            <b-spinner label="Spinning" small></b-spinner>
            <strong class="mr-auto pl-3"> Sync data with FINA ...</strong>
            <small class="text-muted mr-2">
              <time-ago locale="id"></time-ago>
            </small>
          </div>
        </template> Sedang menyinkronkan data {{config.title}} dengan aplikasi FINA-KIW. Harap tunggu sebentar.
      </b-toast>
    </template>
    <template v-slot:header-table>
      <div class="card-body py-0">
        <div class="alert alert-custom alert-light-primary p-2 mb-0" role="alert">
          <div class="alert-icon">
            <span class="svg-icon svg-icon-primary svg-icon-xl">
              <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <rect x="0" y="0" width="24" height="24" />
                  <path d="M5,2 L19,2 C20.1045695,2 21,2.8954305 21,4 L21,6 C21,7.1045695 20.1045695,8 19,8 L5,8 C3.8954305,8 3,7.1045695 3,6 L3,4 C3,2.8954305 3.8954305,2 5,2 Z M11,4 C10.4477153,4 10,4.44771525 10,5 C10,5.55228475 10.4477153,6 11,6 L16,6 C16.5522847,6 17,5.55228475 17,5 C17,4.44771525 16.5522847,4 16,4 L11,4 Z M7,6 C7.55228475,6 8,5.55228475 8,5 C8,4.44771525 7.55228475,4 7,4 C6.44771525,4 6,4.44771525 6,5 C6,5.55228475 6.44771525,6 7,6 Z" fill="#000000" opacity="0.3" />
                  <path d="M5,9 L19,9 C20.1045695,9 21,9.8954305 21,11 L21,13 C21,14.1045695 20.1045695,15 19,15 L5,15 C3.8954305,15 3,14.1045695 3,13 L3,11 C3,9.8954305 3.8954305,9 5,9 Z M11,11 C10.4477153,11 10,11.4477153 10,12 C10,12.5522847 10.4477153,13 11,13 L16,13 C16.5522847,13 17,12.5522847 17,12 C17,11.4477153 16.5522847,11 16,11 L11,11 Z M7,13 C7.55228475,13 8,12.5522847 8,12 C8,11.4477153 7.55228475,11 7,11 C6.44771525,11 6,11.4477153 6,12 C6,12.5522847 6.44771525,13 7,13 Z" fill="#000000" />
                  <path d="M5,16 L19,16 C20.1045695,16 21,16.8954305 21,18 L21,20 C21,21.1045695 20.1045695,22 19,22 L5,22 C3.8954305,22 3,21.1045695 3,20 L3,18 C3,16.8954305 3.8954305,16 5,16 Z M11,18 C10.4477153,18 10,18.4477153 10,19 C10,19.5522847 10.4477153,20 11,20 L16,20 C16.5522847,20 17,19.5522847 17,19 C17,18.4477153 16.5522847,18 16,18 L11,18 Z M7,20 C7.55228475,20 8,19.5522847 8,19 C8,18.4477153 7.55228475,18 7,18 C6.44771525,18 6,18.4477153 6,19 C6,19.5522847 6.44771525,20 7,20 Z" fill="#000000" />
                </g>
              </svg>
            </span>
          </div>
          <div class="alert-text">Terakhir disinkronkan - <time-ago :datetime="lastSync.sync_at || new Date()" locale="id"></time-ago>, oleh {{ lastSync.rel_sync_by || 'System' }}</div>
        </div>
      </div>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-fina-salary-accounts',
  data () {
    return {
      config: {
        title: 'Akun Penggajian Fina',
        model_api: 'fina-salary-accounts',
        getter: 'fina-salary-accounts',
        setter: 'fina-salary-accounts',
        pk_field: null,
        permission: {
          create: 'create-fina-salary-accounts',
          read: 'view-fina-salary-accounts',
          update: 'update-fina-salary-accounts',
          delete: 'delete-fina-salary-accounts'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      },
      isLoadingSync: false,
      lastSync: {}
    }
  },
  mounted () {
    this.getLastSync()
  },
  methods: {
    syncData () {
      this.isLoadingSync = true
      this.$_api.post('custom/fina-salary-accounts/sync-data').then(res => {
        this.isLoadingSync = false
        this.lastSync = Object.assign({}, res.data)
        this.$refs['sync-module'].$children[1].getData()
      }).catch((err) => {
        this.isLoadingSync = false
        this.$_alert.error(err)
      })
    },
    getLastSync () {
      this.$_api.get('custom/log-sync-fina-salary-accounts/last-sync-data').then(res => {
        this.lastSync = Object.assign({}, res.data)
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



